@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Caveat', 'DM Sans', 'Permanent Marker', 
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Roboto",-apple-system,system-ui,sans-serif;padding:0;margin:0;border:0;scroll-behavior:smooth}*,*:before,*:after{box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}html{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-family:inherit}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:400}.app{min-height:100vh;background-color:#8394b5;color:#fff;padding-top:130px;padding-bottom:140px}@media(max-width: 575px){.app{padding-top:100px}}.pageTitle{font-family:"Permanent Marker";text-transform:uppercase;display:flex;justify-content:center;font-size:4vw;margin-bottom:30px;color:#b0e4ff}@media(max-width: 1199px){.pageTitle{font-size:6vw}}@media(max-width: 575px){.pageTitle{margin-bottom:15px}}
.headerTitle{cursor:pointer;font-family:"Permanent Marker";display:flex;justify-content:center;font-size:50px}.header{padding:15px;box-shadow:0px 1px 5px #333;color:#aea7a7;position:fixed;width:100%;z-index:100;background-color:#c7daff;font-family:"Montserrat",sans-serif;font-weight:400;margin-bottom:50px}@media(max-width: 1199px){.headerTitle{font-size:6vw}}
.media{display:flex;flex-direction:column;max-width:300px;height:550px;padding:10px;margin:30px 30px;background-color:#cbd5e8;border-radius:10px;position:relative;font-family:"Monserrat",sans-serif;cursor:pointer}.media:hover{background-color:#fff;color:#333;transition:1.2s ease-in-out}.media .poster{height:70%;border-radius:10px}.media .title{width:100%;text-align:center;font-size:22px;padding:15px 0;color:#6d66e6}.media .subTitle{font-size:22px;margin-bottom:10px;text-align:center;color:#3930e9}.media .date{font-size:22px;text-align:center;margin-bottom:10px;color:#0e097c}
.trending{display:flex;flex-wrap:wrap;justify-content:space-around}
.movies{display:flex;flex-wrap:wrap;justify-content:space-around}
.tvSeries{display:flex;flex-wrap:wrap;justify-content:space-around}
.personCardWrapper{display:flex;flex-wrap:wrap;justify-content:space-around}.personCard{display:flex;flex-direction:column;max-width:300px;padding:7px;margin:30px 30px;background-color:#cbd5e8;border-radius:10px;position:relative;font-family:"Monserrat",sans-serif;cursor:pointer}.personCard .person-name{font-family:"DM Sans";font-size:20px;margin:10px 0;color:#535594}.personCard .person-popularity{font-size:15px;color:#535594}.personCard .poster{border-radius:10px}
.wrapper-content__top{display:flex;margin-bottom:30px;background-color:rgba(169,167,199,.761);padding:20px}@media(max-width: 767px){.wrapper-content__top{flex-direction:column}}.content-right{flex-basis:80%;padding:0 20px}.content-right__wrapper{display:flex;flex-direction:column;justify-content:center;text-align:center}.content-right__title{font-size:50px;margin-bottom:20px;color:#273376;font-family:"Rubik Beastly"}@media(max-width: 575px){.content-right__title{font-size:30px;margin-bottom:10px}}.content-right__subtitle{font-family:"Shadows Into Light";margin-bottom:20px;font-size:20px;color:#1a2405;font-family:"Caveat"}.content-right__date{font-size:30px;margin-bottom:30px;color:#46358f}@media(max-width: 575px){.content-right__date{font-size:25px;margin-bottom:10px}}.content-right__media_type{font-size:40px;margin-bottom:20px}.content-right__description{font-size:25px;color:#efe9f0}@media(max-width: 575px){.content-right__description{font-size:20px}}.content-right__genres{margin-bottom:10px;font-size:20px;color:#253488}.content-right__country{margin-bottom:10px;font-size:20px;color:#f0e1c8}@media(max-width: 767px){.conten-left{margin:0 auto;margin-bottom:15px}}.content-left__img{border-radius:10px;max-width:300px}@media(max-width: 767px){.content-left__img{max-width:200px}}.carousel{height:400px}.carousel-title{margin-bottom:20px;font-size:30px;color:#300404}.companies-wrapper{display:flex}.button-wrapper{display:flex;align-items:center;justify-content:center;margin-top:100px}.content-button{width:500px;height:100px;border-radius:30px}
@media(max-width: 550px){.alice-carousel__stage-item{margin:2px}}
.carouselItem{display:flex;flex-direction:column;object-fit:contain;padding:10px;margin-bottom:20px;width:200px;height:350px}.carouselItem__txt{color:#333;font-size:20px}@media(max-width: 575px){.carouselItem{justify-content:center;align-items:center;margin:0}}.carouselItem__img{border-radius:10px;margin-bottom:5px;box-shadow:0px 0px 5px #000}
.content-about__budget{font-size:20px;margin-bottom:20px;color:#044604}@media(max-width: 575px){.content-about__budget{font-size:18px}}.content-about__revenue{margin-bottom:20px;font-size:20px;color:#044604}@media(max-width: 575px){.content-about__revenue{font-size:18px}}.content-about__time{margin-bottom:20px;font-size:20px;color:#c44e4e}.content-about__web{margin-bottom:20px;font-size:23px;color:#8000ff}@media(max-width: 575px){.content-about__web{font-size:16px}}.content-about__web a{color:#84ebe6;transition:all .8s ease;word-wrap:break-word}.content-about__web a:hover{color:#e4c3c3}.content-about .companies-wrapper{display:flex}@media(max-width: 767px){.content-about .companies-wrapper{flex-direction:column}}.content-about__companies{margin-bottom:20px}.content-about__companies .company{margin-bottom:15px;font-size:20px;color:#e1b3b3}@media(max-width: 575px){.content-about__companies .company{font-size:18px}}.content-about__img{display:flex;align-items:center;margin-left:90px;padding-bottom:15px}.content-about__img img{margin-right:50px;margin-bottom:15px}@media(max-width: 767px){.content-about__img img{margin-right:10px}}@media(max-width: 767px){.content-about__img{flex-wrap:wrap;margin:0}}
.bio-wrapper{padding-top:30px}.bio-wrapper .bio-top{display:flex;margin-bottom:40px}@media(max-width: 768px){.bio-wrapper .bio-top{flex-direction:column}}@media(max-width: 768px){.bio-wrapper .bio-top__left{display:flex;justify-content:center}}.bio-wrapper .bio-top__right{flex-basis:70%;text-align:center}.bio-wrapper .bio-top__right .person-name{font-size:60px;margin-bottom:20px}@media(max-width: 992px){.bio-wrapper .bio-top__right .person-name{font-size:50px}}@media(max-width: 575px){.bio-wrapper .bio-top__right .person-name{font-size:40px}}.bio-wrapper .bio-top__right .person-birthday{font-size:30px;margin-bottom:20px}.bio-wrapper .bio-top__right .person-place_of_birth{font-size:25px;margin-bottom:20px}.bio-wrapper .bio-top__right .person-cast__title{font-size:30px;color:#29285a;margin-bottom:20px}.bio-wrapper .bio-top__right .person-cast li{margin-bottom:15px;font-size:20px}.bio-wrapper .bio-bottom .bio-biography{font-size:20px;line-height:25px;color:#b3dcf8}

.navWrapper{display:flex;justify-content:space-between;padding:0 30px}.nav-link{display:flex;align-items:center;text-transform:uppercase;margin-left:5px;color:#9d8181;transition:all .8s ease}.nav-link:hover{color:#311}@media(max-width: 767px){.nav-link span{display:none}}.nav-link .nav-icon{margin-right:10px}
.footer{position:fixed;left:0;bottom:0;width:100%;z-index:5000;background-color:#c7daff;padding:10px 15px;box-shadow:0px 1px 5px #333;margin-top:20px}
